  height: 100%;
  opacity: 1;
  z-index: 2;
  background: #fff3;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.image-wrapper:hover:before {
  opacity: 0;
}

.image-wrapper img, .image-wrapper iframe {
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: transform .3s;
  position: absolute;
  top: 0;
